.product {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-between;
  border-top: 1px solid #D5D5D5;

  &_card {
    border: 1px solid #fff;
    width: 21%;
    margin-top: 2%;
    // display: flex;
    text-decoration: none;
    align-items: center;
    margin: 0 auto;

    &-list {
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
      margin-bottom: 20px;
    }

    &:hover {
      opacity: 0.2;
      border: 1px solid #D5D5D5;
    }
  }

  &_wrapper {
    display: flex;
    flex-direction: column;
  }

  &_title {
    color: #000;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
  }

  &_category {
    color: #4a4a4a;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }

  &_price {
    color: #000;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
  }
}
