.wrapper {
  display: flex;
  flex-direction: column;

  &_top {
    display: flex;
    width: 70%;
    justify-content: space-between;
    margin-left: 100px;

    &-text {
      color: #414d6a;
      font-size: 17px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
    }
    &-list {
      display: flex;
      width: 60%;
      justify-content: space-between;
      margin-top: 30px;
      margin-left: 130px;

      &-text {
        color: #000;
        font-size: 14px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
      }
    }
  }
}
.cards{
  display: flex;
  width: 90%;
  margin: 0 auto;
}
