.login {
  display: flex;
  width: 90%;
  height: 800px;
  align-items: center;
  margin: 0 auto;

  &_form {
    display: flex;
    flex-direction: column;
    width: 40%;
    margin: 0 auto;

    &-inpass {
      width: 329px;
      height: 62px;
      text-align: justify;
      font-size: 17px;
      font-style: normal;
      font-weight: 300;
      line-height: normal;
    }

    &-text {
      color: #787993;
      margin-top: 30px;

      font-size: 17px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
    }

    &-button {
      width: 329px;
      height: 62px;
      border-radius: 5px;
      background: #41d481;
      text-align: center;
        cursor: pointer;
      color: #fff;
      text-align: center;
      font-size: 17px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
      margin-top: 20px;
      transition: 0.5s;
      border: 2px solid #fff;

      &:hover {
        background: #fff;
        color: #41d481;
        border: 2px solid #41d481;
      }
    }
  }
}

.right {
  width: 50%;
  height: 800px;
  background-image: url(../../Assets/Images/loginBG.png);
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;

  &_card {
    width: 275px;
    height: 275px;
    border-radius: 220px 220px 220px 25px;
    background: #fff;
    position: absolute;
    top: 400px;
    left: 100px;
    display: flex;
    flex-direction: column;
    padding-top: 80px;
    padding-left: 30px;

    &-img {
      width: 50px;
      height: 26px;
      margin-bottom: 15px;
    }

    &-title {
      width: 213px;
      color: #787993;
      font-size: 19px;
      font-style: normal;
      font-weight: 700;
      line-height: 21px;
      margin-bottom: 15px;
    }
    &-button {
      width: 162px;
      height: 40px;
      color: #41d481;
      font-size: 16px;
      font-style: normal;
      font-weight: 700;
      line-height: normal;
      border: 2px solid #41d481;
      cursor: pointer;
      border-radius: 5px;
      transition: 0.5s;

      &:hover{
        background: #41d481;
        color: #fff;
      }
    }
  }
}
