.footer {
  display: block;
  width: 100%;
  height: 112px;
  background-color: #282b30;
  flex-grow: 1;
  margin-top: 50px;

  &_top {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &_first {
      display: flex;
      align-items: center;

      &-img {
        width: 67px;
        height: 67px;
      }
      &-text {
        color: #fff;
        font-size: 14px;
        font-style: normal;
        font-weight: 700;
        line-height: 17px;
        width: 170px;
        margin-left: 30px;
      }
    }
  }
  &_bottom{
   
    display: flex;

  }
}
.text {
    width: 307px;
  color: #98a4b7;
  font-size: 15px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}
