.navbar {
  display: flex;
  align-items: center;
  width: 100%;
  height: 67px;
  justify-content: space-between;

  &_link {
    display: flex;
    text-decoration: none;
    align-items: center;
  }

  &_el_wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 606px;
    height: auto;
    margin-left: 30px;
  }

  &_el_link {
    color: #000;
    font-size: 15px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    text-decoration: none;
  }

  &_text {
    width: 172px;
    height: 34px;
    font-size: 15px;
    font-style: normal;
    font-weight: 700;
    line-height: 17px;
    color: #000;
    margin-left: 30px;
  }
  &_logo {
    width: 67px;
    height: 67px;
  }

  &_reg_button {
    color: #4e72ca;
    font-size: 17px;
    font-style: normal;
    font-weight: 700;
  }
  &_lang_button {
    color: #4a4a4a;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
  }
}

.profile {
  display: flex;
  align-items: center;



  &_button {
    width: 41px;
    height: 41px;
    border-radius: 20.5px;
    border: 1px solid #979797;
    background-image: url(../../Assets/Images/profileImg.jpg);
    background-repeat: no-repeat;
    background-size: cover;

  

  }
  &_text {
    color: #787993;
    font-size: 17px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
  }

  &_active{
    width: 50px;
    height: 60px;
    border: 1px solid #000;
    position: absolute;
    right: 0;
    top: 50px;
  }
}

