.el_product {
  display: flex;
  width: 90%;
  flex-direction: column;
  margin: 0 auto;
  padding-top: 50px;

  &-title {
    color: #414d6a;
    font-size: 34px !important;
    font-style: normal;
    line-height: normal;
    display: flex;
    margin-bottom: 30px;
  }

  &-wrapper {
    width: 90%;
    height: auto;
    display: flex;
    flex-direction: row;
  }

  &-image {
    width: 40%;
    display: flex;
    margin-right: 50px;
  }

  &-content {
    width: 60%;
    display: flex;
    flex-direction: column;
    margin-top: 70px;

    &-price {
      color: #444;
      font-size: 40px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
    }

    &-des {
      width: 70%;
      font-size: 30px;
    }

    &-cat {
      font-size: 30px;
      margin-top: 30px;
      text-decoration: underline;
    }
  }
  &-button {
    width: 90%;
    height: 30px;
    margin: 0 auto;
    text-align: center;
    background: #41d481;
    text-decoration: none;
    color: #fff;
    margin-top: 20px;
  }
}
